	module.exports = {"data":{"allTalkJson":{"edges":[{"node":{"description":"Some teams are orders of magnitude more effective than others, turning around business solutions in days or even hours. Their secret is a combination of smart technology choices, great development habits and a powerful team dynamic. In this talk Dan describes a number of patterns of behaviour that he's identified working with some great teams, beyond the basics of co-location, stand-ups and pair rotation. You'll gain a new appreciation for old techniques like code reviews, and even working in silos won't seem so bad!","meta":{"duration":"PT51M4S"},"presenter":{"name":"Dan North"},"source":{"id":"lvs7VEsQzKY","provider":"youtube"},"slug":"patterns-of-effective-teams","tag":["GOTO Conference 2017","Agile"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/lvs7VEsQzKY/default.jpg","height":90,"width":120}},"title":"Patterns of Effective Teams"}}]}},"pathContext":{"speaker":"Dan North"}}

